.materials-center-list {
    width: 77%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    overflow-y: auto;
}

.materials-center-list::-webkit-scrollbar {
    width: 6px;
}

.materials-center-list::-webkit-scrollbar-track {
    background: #EDEDED;
    border-radius: 4px;
}

.materials-center-list::-webkit-scrollbar-thumb {
    background: #D8D8D8;
    border-radius: 4px;
}

.single-materials-center-list {
    -ms-flex: auto;
        flex: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-right: 2px solid #F9F9F9;
    cursor: pointer;
}

.single-materials-center-list img {
    max-width: 100%;
    width: 67px;
    height: 67px;
}

.single-materials-center-list h3 {
    height: 26px;
    color: #2D2D2D;
    font-family: Montserrat, sans-serif;
    font-size: 12px;
    letter-spacing: 1.6px;
    line-height: 16px;
    text-align: center;
}

.single-materials-center-list-child {
    display: -ms-flexbox;
    display: flex;
    height: 50%;
    width: 11.1%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-right: 2px solid #F9F9F9;
    border-bottom: 2px solid #F9F9F9;
    cursor: pointer;
    /*position: relative;*/
}

.single-materials-center-hover {
    position: absolute;
    width: 195px;
    height: 242px;
    border: 1px solid #E5E5E5;
    text-align: center;
    padding-top: 18px;
    background-color: #FFFFFF;
}

.single-materials-center-hover::after {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    border-top: 15px solid #FFFFFF;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
}

.single-materials-center-hover::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    border-top: 15px solid #E5E5E5;
    border-left: 17px solid transparent;
    border-right: 17px solid transparent;
}

.single-materials-center-hover img {
    max-width: 100%;
    width: 150px;
    height: 150px;
}

.single-materials-center-list-child-wrapper {
    text-align: center;
}

.single-materials-center-list-child-wrapper img {
    max-width: 100%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.single-materials-center-list-child h3 {
    height: 23px;
    color: #2D2D2D;
    font-family: Montserrat, sans-serif;
    font-size: 10px;
    letter-spacing: 1.33px;
    line-height: 13px;
    text-align: center;
}




